.intro {
    padding-top: 25px;
    overflow: hidden;

    &-container {
        position: relative;
    }
    &-descr{
        font-family: 'ProximaNovaCond' ,sans-serif;
        font-size: 24px;
    }
    &__title{
        z-index: 1;
    }
    &-title {
        color: #000;
        font-size: 145px;
        font-weight: bold;
        text-transform: uppercase;
        position: relative;
        z-index: 2;
        line-height: 0.6;
        margin-bottom: 45px;

        >span:nth-child(1){
            position: relative;
            line-height: 1.2;
            background: var(--c-primary);
            display: inline-flex;
            align-items: center;
            span{
                top: 0.05em;
                display: inline-block;
                position: relative;
                z-index: 2;
            }
            &::after{
                content: '';
                position: absolute;
                top: 0;
                left: -100%;
                width: 1000%;
                height: 100%;
                display: inline-block;
                background: var(--c-primary);
                z-index: 1;
            }
        }

        >span:last-child{
            font-size: 80px;
        }
    }
    &__baner{
        position: relative;
        z-index: 3;
        margin-top: -25px;
        &-container {
            border: 5px solid #fff;
        }
    }
    &__info{
        display: flex;

        &-container{
            margin-top: 40px;
            display: none;
        }
        &-left{
            display: flex;
            row-gap: 10px;
            flex-direction: column;
        }
        &-right{
            display: flex;
            row-gap: 10px;
            flex-direction: column;
        }
        &__item {
            display: flex;
            align-items: center;
            gap: 15px;


            &-num{
                transition: 0.3s ease-in;
                cursor: pointer;
                font-size: 30px;
                font-weight: 700;
                color: #000;
                &:hover{
                    opacity: 0.7;
                    color: inherit;
                }

            
            }
            &-text{
                font-family: 'ProximaNovaCond',sans-serif;
                font-size: 15px;
                line-height: 1;

            }
        }

        &__phone,
        &__address{
            display: flex;
        }
    }
    &__item-icon{
        display: flex;
        flex-shrink: 0;
        align-items: center;
        justify-content: center;
        width: 36px;
        height: 36px;
        border-radius: 50%;
        background-color: #fff;

        i {
            font-size: 14px;
            color: var(--c-primary);
        }
    }
}